export const fetchRanking = async () => {
    try {
      const res = await fetch('http://localhost:8000/api/trend/all/');
      if (res.ok) {
        const data = await res.json();
        return data.articles || [];
      } else {
        console.error('');
        return [];
      }
    } catch (err) {
      console.error(':', err);
      return [];
    }
  };
  